body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #D8D8D8;
    padding: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-content {
    height: 90px;
}

.card-content p {
    overflow: hidden;
    position: relative;
    line-height: 1.2em;
    max-height: 3.6em;
    text-align: justify;
    margin-right: -1em;
    padding-right: 1em;
}

.card-content p:before {
    content: '...';
    position: absolute;
    right: 0;
    bottom: 0;
}

.card-content p:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: white;
}

.card-action {
    padding: 10px !important;
    background-color: #1f5cbc !important;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
}
.fan-title {
    position: absolute;
    top: 0;
    left: 35px;
    font-size: 65px;
    color: rgb(77, 87, 114);
    font-family: garnet;
}
.modal {
    background-color: #ffffff;
    top: 0 !important;
    max-height: 100% !important;
    width: 100% !important;
    height: 100% !important;
}

.modal-content {
    height: 100%;
    width: 100%;
    padding: 0 !important;
    overflow: hidden;
}

.basic-control-wrapper.control-wrapper {
    position: absolute;
    bottom: 25px;
    right: 25px;
}

.basic-control-wrapper .icon-wrapper {
    display: inline-block;
    padding: 15px;
    cursor: pointer;
    position: relative;
}

.basic-control-wrapper .basic-icon-tooltip {
    position: absolute;
    top: -25px;
    width: 100%;
    text-align: center;
    left: 0px;
    color: rgb(77, 87, 114);
    font-family: garnet;
}

.basic-control-wrapper .basic-icon-tooltip.hide-tooltip{
    display: none;
}

/* .basic-control-wrapper .icon-wrapper > img:hover {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
} */

.remote-control-wrapper.control-wrapper {
    position: absolute;
    top: calc((100vh - 388px) / 2);
    right: 50px;
    -webkit-user-select: none;
            user-select: none;
}

.remote-hover-text {
    font-size: 15px;
    font-family: garnet;
    color: rgb(77, 87, 114);
    text-align: center;
    top: -85px;
    width: 100%;
    position: absolute;
}

.remote-hover-subtext {
    position: absolute;
    font-size: 12px;
    font-family: garnet;
    color: rgb(77, 87, 114);
    text-align: center;
    word-break: break-word;
    top: -60px;
    width: 100%;
}

.remote-control-wrapper.control-wrapper > img.bg-img {
    width: 229.92px;
    pointer-events: none;
}
.remote-control-wrapper.control-wrapper > img.remote-led {
    width: 4.8px;
    height: 4.8px;
    pointer-events: none;
    position: absolute;
    left: 71.2px;
    top: 54.4px;
}

.remote-icon-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
}

.remote-control-wrapper .left-icon {
    position: absolute;
    left: 67.92px;
    width: 48.24px;
    height: auto;
    cursor: pointer;
}

.remote-control-wrapper .right-icon{
    width: 48px;
    height: auto;
    position: absolute;
    right: 66.72px;
    cursor: pointer;
}

.fan-off, .light-toggle {
    top: 66.24px;
    height: 46.08px !important;
}
.fan-high, .fan-delay {
    top: 121.92px;
}
.fan-med, .fan-nature {
    top: 197.04px;
}
.fan-low, .fan-home {
    top: 271.68px;
}

.countdown-timer-wrapper {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 50%;
    right: 50%;
    -webkit-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
    background: white;
    border-radius: 50% 50%;
}
  
.countdown-timer {
    font-family: "Montserrat";
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 15px;
}

.countdown-text {
    color: rgb(77, 87, 114);
    font-size: 11px;
    font-family: garnet;
}

.countdown-value {
    font-size: 40px;
    color: rgb(77, 87, 114);
}  

h4 {
    font-weight: 700
}

iframe {
    width: 100%;
    height: 100%
}

.model_tags {
    display: flex;
    margin-bottom: 10px;
    flex-wrap: wrap;
}

.model_tags .badge{
    margin-top: 5px;
}

span.badge {
    flex: 0 0 auto;
    line-height: 20px;
    margin-left: 0;
    margin-right: 5px;
}
.placeholder {
    position: absolute;
    width: 455px;
    top: 50%;
    left: 50%;
    margin-left: -225px;
    margin-top: -25px;
    font-size: 24px;
    font-weight: 900;
}

.loading {
    position: absolute;
    width: 100px;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -25px;
    font-size: 24px;
    font-weight: 900;
}

.navigation {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    justify-content: flex-end;
}

.navigation button.active {
    border: 1px solid #1f5cbc;
    background-color: #fff;
    color: #1f5cbc;
    font-weight: 600;
}

.auth {
    display: inline-flex;
    align-items: center;
    margin-right: auto;
}

.auth i {
    margin-right: 5px;
}

.pagination {
    display: inline-flex;
    margin: 20px 0px;
    padding: 0 !important;
    width: 200px;
    justify-content: flex-end;
}

.pagination span {
    display: flex;
    align-content: center;
    padding: 0 15px;
    cursor: pointer;
    font-weight: 700;
}

.pagination span:first-of-type {
    border-right: 1px solid #000;
}

.pagination span:last-of-type {
    padding-right: 0;
}

.search {
    position: relative;
    margin: 20px 0px;
}

.search input {
    padding: 0px 0px 0px 30px !important;
    margin: 0px !important;
    height: 30px !important;
}

.search input:focus {
    border-bottom: 1px solid #1f5cbc !important;
    box-shadow: 0 1px 0 0 #1f5cbc !important;
}

::-webkit-input-placeholder {
    color: #b2b2b2 !important;
    opacity: 1; /* Firefox */
}

::placeholder {
    color: #b2b2b2 !important;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #b2b2b2 !important;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: #b2b2b2 !important;
}

.search i {
    position: absolute;
    left: 10px;
    top: 5px;
    font-size: 22px;
}
@font-face {
    font-family: garnet;
    src: url(/static/media/Garnet-Capitals-Bold.19669c6f.otf)
}

p {
    font-size: 12px;
    margin: 10px 0;
}

label {
    color: #787878;
    font-weight: 700
}

.btn {
    background-color: #1f5cbc
}

.btn:hover, .btn:focus {
    background-color: #143c7a
}

button {
    margin: 0 5px;
}

.row {
    margin-top: 10px;
    margin-bottom: 10px;
}

.divider {
    height: 2px;
    overflow: hidden;
    background-color: #000;
}

.disabled {
    opacity: 0.5;
    pointer-events: none;
    cursor: not-allowed;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */ /* Internet Explorer/Edge */
    user-select: none;
}
